body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex { display: flex; }

.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1 1; }
.flex-2 { flex: 2 1; }
.flex-3 { flex: 3 1; }
.flex-4 { flex: 4 1; }
.flex-5 { flex: 5 1; }
.flex-6 { flex: 6 1; }

.text-center { text-align: center; }

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-end {
  justify-content: flex-end;
  align-items: center;
}

/* CONTAINER */
.container, .container-big, .container-small {
  width: calc(100% - 20px);
  padding: 10px;
  max-width: 1024px;
  margin: 0 auto;
}
.container-big {
  max-width: 1300px;
}
.container-small {
  max-width: 600px;
}

.hr {
  background-color: #999;
  height: 1px;
  margin: 5px 0;
}

/* COMPONENTS */
.TopBar {
  /* background-color: #222; */
  background-color: #B52E09;
  /* background-color: rgb(91, 6, 0); */
  color: #FFFFFF;
  font-size: 0.75rem;
  font-weight: bold;
  line-height: 0px;
}

.Header {
  max-height: 100px;
}
.input-pesquisa {
  width: 95%;
  padding: 10px 15px;
  border-radius: 10px;
  border: 1px solid #999;
  margin-left: 20px;
  font-size: 0.9rem; }
.btn-pesquisa {
  padding: 0;
  background-color: transparent;
  margin-left: -30px;
  border: 0;
  font-size: 1.1rem;
  color: #9F0B00;
  cursor: pointer; }

.itens-header {
  color: #9F0B00;
}
.item-header:first-child span {
  padding: 0 10px;
  font-size: 0.85rem;
}
.item-header-child {
  /* background-color: #9F0B00; */
  color: #FFFFFF;
  background-color: #AB4309;
  padding: 10px 30px;
  border-radius: 20px;
  font-size: 1.2rem;
  cursor: pointer;
}
.item-header-child span {
  font-size: 0.9rem;
  padding: 0 10px;
}

.Categorias {
  /* background-color: rgb(91, 6, 0); */
  /* background-color: #9F0B00; */
  background-color: #B52E09;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 0.85rem;
}
.categoria {
  cursor: pointer;
  border-radius: 20px;
  padding: 4px;
  margin: 1px;
}
.categoria:hover {
  background-color: rgb(184, 57, 48);
}

.breadcrumbs {
  font-size: 0.9rem;
  color: #999;
  margin: 20px 0;
}

.main-pic {
  width: auto;
  height: 600px;
}
.mini-pic-container {
  cursor: pointer;
  border: 1px solid #cccc;
  max-width: 100px;
  margin: 0 10px;
}

.cep-container h4 {
  margin-bottom: 8px; }
.cep-container input {
  border-radius: 10px;
  border: 1px solid #aaa;
  margin: 0 10px;
  padding: 5px 10px;
  font-size: 0.95rem;
  width: 100px; }
.btn-cep {
  background-color: #444;
  color: #FEFEFE;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  padding: 5px 15px;
  cursor: pointer; }
.btn-cep:hover {
  background-color: #666; }


.titulo h2 {
  font-size: 1.8rem;
  margin: 0;
}
.titulo small {
  color:#777;
}

.avaliacoes {
  margin: 10px 0;
}
.valor-avaliacoes {
  margin: 0 5px;
  font-size: 0.9rem;
  color: #666;
}

.price {
  margin: 10px 0;
  color: #777;
}
.price .full {
  text-decoration: line-through;
  font-size: 0.9rem;
  color: #777;
}
.price .valido {
  font-size: 2rem;
  margin: 5px 0 0 0;
  color: #171717;
  font-weight: bold;
}

.btn-cta {
  color: #FAFAFA;
  background-color: #9F0B00;
  padding: 15px 25px;
  margin: 30px 0;
  width: 100%;
  font-size: 1.3rem;
  font-weight: bold;
  border: 0;
  cursor: pointer;
}
.btn-cta:hover {
  background-color: rgb(174, 22, 11);
}

.Descricao img {
  max-width: 300px;
}
.Descricao span {
  line-height: 30px;
  margin: 10px 0;
}

.Avaliacoes {
  padding: 20px 0 100px 0;
}

.cart-avaliacoes {
  max-width: 200px;
  margin: 20px auto 30px auto;
  background-color: #222;
  color: #fff;
  padding: 10px 20px;
  border-radius: 10px;
}
.cart-avaliacoes h2 {
  font-size: 2.2rem;
  margin: 10px;
}
.cart-avaliacoes i {
  font-size: 1.5rem;
}
.no-margin {
  margin: 0;
}
.text-gray {
  color: #999;
}

.Avaliacoes .card {
  /* padding: 20px 10px; */
  /* border: solid rgba(34, 34, 34, 0.434) 1px; */
  margin: 0 20px;
  border-radius: 20px;  
}
.card-header {
  height: 20px;
  background-color: #222;
  padding: 12px 10px 5px 10px;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  color: #FFF;
  font-size: 0.9rem;
}
.fa-star {
  color: rgb(205, 187, 0);
}
.card-body {
  border: 1px solid #111a;
  border-top: 0;
  border-bottom: 0;
  padding: 10px;
}
.card-footer {
  background-color: #222;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 5px 10px 12px 10px;
  color: #FFF;
}

.Institucionais {
  background-color: #222;
  padding: 30px 0 60px 0;
  color: #FAFAFA;
}
.Institucionais h3 {
  color: rgb(206, 14, 0);
  font-size: 1.2rem;
}
.Institucionais span {
  color: #FFF;
  font-size: 0.9rem;
  line-height: 17px;
  cursor: pointer;
}
.Institucionais span:hover {
  text-decoration: underline;
}
.forma {
  padding: 3px;
  border-radius: 10px;
  max-width: 60px;
}

.Footer {
  background-color: #111;
  color: #FAFAFA;
  font-size: 0.85rem;
}

.no-mb { display: block; }
.no-mb-flex { display: flex; }
.only-mb { display: none; }
@media screen and (max-width: 720px){
  .no-mb, .no-mb-flex { display: none; }
  .only-mb { display: flex; }

  .input-pesquisa {
    margin-left: -10px;
    width: 85%; }
  .Header {
    max-height: 150px; }

  .Avaliacoes .card {
    margin: 10px; }
  .main-pic {
    width: 95%;
    height: auto; }

  .HeroShop .titulo {
    margin-top: 20px; }
}
